body {
  margin: 0;
}

.container {
  width: 100%;
  height: 70px;
  background-color: #87cefa;
  box-shadow: 0 2px 10px rgba(0, 0, 0, .54);
}

.search-form {
  text-align: center;
  padding-top: 20px;
}

input {
  height: 25px;
  width: 400px;
  border: 1px solid #789;
  border-radius: 4px;
  outline: none;
  padding: 5px;
  font-size: 14px;
}

button {
  height: 36px;
  cursor: pointer;
  border: 1px solid #789;
  border-radius: 4px;
  margin-left: -5px;
  font-size: 14px;
}

button:hover {
  background-color: #eee8aa;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

